// MENU RESPONSIVE ABRIR / CERRAR

const btnAbrir = document.querySelector('#open');
const btnCerrar = document.querySelector('#close');
const responsiveMenu = document.querySelector('#responsive-menu')

function abrirMenu() {
    btnAbrir.classList.add('header__close');
    btnCerrar.classList.remove('header__close');
    responsiveMenu.classList.add('header__close02')
}

function cerrarMenu() {
    btnCerrar.classList.add('header__close');
    btnAbrir.classList.remove('header__close');
    responsiveMenu.classList.remove('header__close02')
}

btnAbrir.addEventListener('click', abrirMenu);
btnCerrar.addEventListener('click', cerrarMenu);

// SECCION IMAGENES DESKTOP

const botones = document.querySelectorAll('.boton');
const contenidos = document.querySelectorAll('.info__grid-img-none');
const claseBotonActivo = 'activo';
const claseContenidoVisible = 'mostrar';

function quitarTodosLosBotonesActivos () {
    botones.forEach(function(boton) {
        boton.classList.remove(claseBotonActivo);
    });
}

function quitarTodoslosContenidosVisibles() {
    contenidos.forEach(function(contenido) {
        contenido.classList.remove(claseContenidoVisible);
    });
}

function anyadirActivoABoton(posicion, clase) {
    botones[posicion].classList.add(clase);
}

function mostrarContenido(posicion, clase) {
    contenidos[posicion].classList.add(clase);
}


botones.forEach(function(boton, posicion) {
    boton.addEventListener('click', function() {
        quitarTodosLosBotonesActivos();
        anyadirActivoABoton(posicion, claseBotonActivo);
        quitarTodoslosContenidosVisibles();
        mostrarContenido(posicion, claseContenidoVisible);
    });
});

